    --subheadlineTextColor: #15CEFF;
    --footerTextColor: #3E3E3E;
    --blue: #15CEFF;
    --green: #41D692;
    --max-width: 1280px;
    --footerBgColor: #f5f5f5;
    --BoxShadow: 0 0 6px #51515120;
    --regular: 400;
    --medium: 500;
    --semiBold: 600;
    --light: 300;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


/* Nav bar start */
#logo-link-container {
    display: contents;
}

#nav-bar-container {
    height: 68px;
    position: fixed;
    z-index: 2;
}

.nav-bar {
    width: 100%;
    max-width: var(--max-width);
    height: 68px;
    border: none;
    position: fixed;
    top: 0;
    background-color: white;
    z-index: 1;
    padding-top: .2rem;
    box-shadow: 0 3px 5px #00000016;
    padding: 6px 1.5rem;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
}

/* Nav bar end */
#overlay {
    position: fixed;
    display: none;
    width: 100%;
    max-width: 768px;
    height: 652px;
    margin: auto;
    top: 32px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    cursor: pointer;
    border-radius: 8px;
    border: 1px solid var(--color-brand-brand-primary, #15CEFF);
    background: rgba(255, 255, 255, 0.50);
    backdrop-filter: blur(10px);
}

/* page container */
