/* COOKIE POPUP STYLES */

.cookie-popup {
    background: #fff;
    color: #000;
    font-size: 0.875rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: fixed;
    bottom: -6.25rem;
    left: 1.25rem;
    right: 1.25rem;
    padding: 0.625rem 0.9375rem;
    box-shadow: 0 0 0.625rem 0 rgba(0, 0, 0, .15);
    line-height: 150%;
    transition: opacity .5s;
    opacity: 0;
}


.cookie-popup--not-accepted {
    opacity: 1;
    animation: cookie-popup-in .5s ease forwards;
}

.cookie-popup--accepted {
    opacity: 0;
}

.cookie-popup a {
    color: skyblue;
}

.cookie-popup a:visited {
    color: skyblue;
    text-decoration: none;
}

.cookie-popup-actions {
    flex: 1;
    text-align: right;
}

.cookie-popup-actions button {
    color: orange;
    border: none;
    font-family: inherit;
    font-style: inherit;
    font-size: inherit;
    font-weight: bold;
    font-size: 0.75rem;
    padding: .5rem;
    margin: .5rem;
    cursor: pointer;
    border-radius: 5px;
    text-decoration: none;
    width: 100px;
}

.cookie-popup-actions #deny {
    color: white;
    background: var(--blue);
}

.cookie-popup-actions #accept {
    color: var(--blue);
    background: white;
    border: 1px solid var(--blue);

}

.cookie-popup-actions button:hover {
    text-decoration: underline;
}

@keyframes cookie-popup-in {
    from {
        bottom: -6.25rem;
    }

    to {
        bottom: 1.25rem;
    }
}

/* Cookies end */