#mob-anim-tagline-txt-container,
#tab-desk-anim-tagline-txt-container {
  overflow: hidden;
}


.lineup1 {
  animation: 2s anim-tagline-up1 .5s ease-out;
  animation-fill-mode: forwards;

}

.lineup2 {
  animation: 2s anim-tagline-up2 1s ease-out;
  animation-fill-mode: forwards;
}

.lineup3 {
  animation: 2s anim-tagline-up3 1.5s ease-out;
  animation-fill-mode: forwards;
}

@keyframes anim-tagline-up1 {
  0% {
    opacity: 0;
    transform: translateY(282%);
  }

  20% {
    opacity: 0;
  }

  50% {
    opacity: 1;
    transform: translateY(0%);
  }

  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

@keyframes anim-tagline-up2 {
  0% {
    opacity: 0;
    transform: translateY(181%);
  }

  20% {
    opacity: 0;
  }

  50% {
    opacity: 1;
    transform: translateY(0%);
  }

  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

@keyframes anim-tagline-up3 {
  0% {
    opacity: 0;
    transform: translateY(82%);
  }

  20% {
    opacity: 0;
  }

  50% {
    opacity: 1;
    transform: translateY(0%);
  }

  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

.mob-landing-static-txt,
#tab-desk-landing-static-txt {
  animation: 5s fadein 0s ease-out;

}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }

}


