/* landing image animations */
.floating {
  animation: example, iconScale linear;
  animation-iteration-count: infinite, 1;
  animation-direction: alternate, normal;
  animation-duration: 5s, 3s;
  animation-timing-function: linear, cubic-bezier(.6, .44, .23, 1.18);
  transform-origin: center;
}

@keyframes example {
  from {
    transform: translateY(-5px);
  }

  to {
    transform: translateY(20px);
  }
}

.iconScale {
  animation: iconScale 2s cubic-bezier(.6, .44, .23, 1.18);
  transform-origin: center;
}

@keyframes iconScale {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

.rotateIcon {
  animation: iconScale, rotate;
  animation-iteration-count: 1, infinite;
  animation-duration: 5s, 3s;
  animation-timing-function: cubic-bezier(.6, .44, .23, 1.18), linear;
  transform-origin: 133px 148px;
  ;
}

.rotateYIcon {
  animation: iconScale, rotateY;
  animation-iteration-count: 1, infinite;
  animation-duration: 5s, 3s;
  animation-timing-function: cubic-bezier(.6, .44, .23, 1.18), cubic-bezier(.25, .1, .12, 1.27);
  transform-origin: 441px 140px;
}

.swimming {
  animation: iconScale, swim;
  animation-timing-function: cubic-bezier(.6, .44, .23, 1.18), linear;
  animation-duration: 5s, 10s;
  transform-origin: center;
  animation-iteration-count: 1, infinite;
  transform-origin: 98px 300px;
}

@keyframes swim {
  0% {
    transform: rotateY(0deg) translateX(0px);
  }

  10% {
    transform: rotateY(50deg);
  }

  20% {
    transform: rotateY(-50deg);
  }

  30% {
    transform: rotateY(0deg) translateX(-80px);
  }

  40% {
    transform: rotateY(-50deg) translateX(-80px);
  }

  60% {
    transform: rotateY(180deg);
  }

  100% {
    transform: translateX(0px);
  }

  /* 
  75% {
    transform: rotateY(130deg);
  }

  100% {
    transform: rotateY(180deg) translateX(50px);
  } */
}

@keyframes rotateY {
  100% {
    transform: rotateY(360deg);
  }
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}