:root {
    --navBorderColor: #0000001e;
    --knockBgColor: #16d6ff;
    --textGreenColor: #00d28f;
    --taglineTextColor: #15CEFF;
    --taglineFullstopColor: #41D692;
    --landingStaticTextColor: #3E3E3E;
    --landingDynamicTextColor: #41D692;
    --headlineTextColor: #15CEFF;
    --bodyTextColor: #3E3E3E;
    --subheadlineTextColor: #15CEFF;
    --footerTextColor: #3E3E3E;
    --blue: #15CEFF;
    --green: #41D692;
    --max-nav-footer-width: 1280px;
    --max-content-width: 1130px;
    --footerBgColor: #f7f7f7;
    --footerBgColor: #f5f5f5;
    --BoxShadow: 0 0 6px #51515120;
    --regular: 400;
    --medium: 500;
    --semiBold: 600;
}

@media only screen and (max-width: 320px) {

    .project-btn-container {
        display: flex;
        justify-content: space-between;
    }

    .project-btn-container>div {
        width: 7rem;
    }

    .project-btn-container>div>button {
        padding: 1rem 0;
        margin-right: 1rem;
    }

    .tagline-txt {
        font-size: 1.3rem;
    }
}

@media only screen and (max-width: 600px) {

    /* portfolio start */
    .portfolio-card {
        width: 100%;
    }

    /* portfolio end */
    #service-main-container .txt-2 {
        font-size: 12px;
    }

    #service-main-container h1 {
        font-size: 20px;
    }

    #service-main-container .txt-1 {
        font-size: 14px;
    }

    #why-choose-us-container h4 {
        font-size: 14px;
        color: var(--green);
    }

    #why-choose-us-wrapper p {
        font-size: 14px;
    }

    #why-choose-us-wrapper {
        margin-top: 1.5rem;
    }

    #why-choose-us-container h1 {
        font-size: 20px;
    }

    #why-choose-us-container h2 {
        font-size: 16px;
    }

    #home-page {
        padding: 1.5rem 1rem 0 1rem;
    }

    .landing-dynamic-txt {
        font-size: 20px;
        letter-spacing: .4px;
    }

    .services-icon {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 40px;
    }

    #why-choose-us-wrapper {
        grid-template-columns: auto;
        row-gap: 1rem;
    }
}

@media only screen and (max-width: 1024px) {
    .service-page-continer {
        padding: 1.5rem 1rem 0 1rem;
    }

    #service-main-container {
        flex-flow: column;
        row-gap: 2rem;
        align-items: center;
    }

    #service-main-wrapper {
        align-items: center;
        text-align: center;
    }

    #animation {
        display: none;
        width: 100%;
    }

    #mob-tagline-txt h1 {
        font-family: 'Roboto Slab', sans-serif;
        font-weight: bold;
        font-size: 45px;
    }

    #mob-body-txt {
        display: none;

    }

    .tab-desk-body-txt {
        line-height: 25px;
        font-family: 'Poppins', sans-serif;
    }

    .services-qutote {
        margin-top: 2.5rem;
        margin-bottom: 2.5rem;
    }



    .sub-headline-txt {
        font-size: 20px;
        margin-top: 2rem;
        margin-bottom: 2rem;
    }

    #desk-privacy-txt-container {
        display: block;
    }

    .privacy-container>h3 {
        font-size: 16px;
        font-weight: var(--medium);
        text-align: left;
    }

    #tab-desk-privacy-txt-container {
        display: block;
    }

    #tab-desk-privacy-txt-container p {
        font-size: 14px;
        line-height: 25px;
    }

    .btn-container {
        flex-direction: column;
        margin: 1rem auto 1.5rem auto;
        gap: 1rem;
        align-items: center;
    }

    #desk-clutch {
        justify-content: center;
    }
}

/* Who We Are */
@media only screen and (min-width: 601px) {

    /* #portfolio-main-container h1,
    h2,
    p,
    h3 {
        text-align: left;
    } */

    #portfolio-main-container h1 {
        font-size: 28px;
