        margin-bottom: 3rem;
    }

    #service-main-container .txt-1 {
        font-size: 20px;
    }

    #service-main-container .txt-2 {
        font-size: 16px;
        font-weight: 400;
    }

    #service-main-container h1 {
        font-size: 28px;
    }

    #why-choose-us-wrapper {
        margin-top: 2rem;
