    }

    #why-choose-us-container h2 {
        font-size: 20px;
    }

    #why-choose-us-container h4 {
        font-size: 1rem;
    }

    #why-choose-us-wrapper {
        grid-template-columns: auto auto;
        justify-content: space-between;
    }

    .contact-form>div {
        margin-bottom: 2rem;
    }

    /* careers */
    .careers-container .careers-img {
        max-width: 423px;
        width: 100%;
        margin: auto;
    }

    .careers-wrapper .txt-wrapper {
        width: 100%;
    }

    .careers-wrapper {
        align-items: center;
        column-gap: 1rem;
    }

    .whoweare-wrapper,
    .whatwedo-wrapper,
    .future-wrapper {
        align-items: center;
    }

    .who-we-are-img {
        width: 100%;
        max-width: 408px;
        margin: auto;
    }

    .who-we-are-txt {
        width: 501px;
    }

    .whoweare-main .body-txt {
        text-align: left;
        font-family: var(--regular) 16px 'Poppins', sans-serif;
        line-height: 21px;
        letter-spacing: 1.12px;
    }

    .whoweare-main {
        max-width: 1072px;
        margin-left: auto;
        margin-right: auto;
    }

    /* our services */
    #our-trusted-allies {
        grid-template-columns: auto auto auto auto;
        padding: 2rem 3rem;
    }

    #allies-last-5 {
        grid-column: 2/3;
    }

    #allies-last-6 {
        grid-column: 3/4;
    }

    .project-details-wrapper {
        width: 90%;
        height: 128px;
        display: flex;
        border: none;
        box-shadow: var(--BoxShadow);
        padding: 16px 16px;
        border-radius: 8px;
        margin-top: 2rem;
        margin-left: auto;
        margin-right: auto;
    }

    .whatwedo-container,
    .future-container {
        margin-top: 0 !important;
    }


    #project-details-2 {
        padding: 5px 20px;
        width: 40% !important;
        height: 56px;
        margin-top: auto;
        margin-bottom: auto;
    }

    #project-details-2 :first-child {
        margin-top: -15px;
    }

    .project-details {
        display: flex;
        flex-direction: column;
        height: fit-content;
    }

    .project-details :first-child {
        font-size: 59px;
    }

    .project-details :last-child {
        font-size: 14px;
        font-weight: var(--regular);
    }

    .project-btn-container {
        display: flex;
        justify-content: space-between;
        margin-bottom: 3rem;
        margin-top: 2rem;
        width: 551px;
        margin-left: auto;
        margin-right: auto;

    }

    .project-btn-container>div {
        width: 229px;
        height: 41px;
    }

    .pro-service-btn,
    .pro-quoto-btn {
        font-size: 16px;

    }

    .pro-service-btn {
        padding: 10px 77px;
    }

    .pro-quoto-btn {
        padding: 10px 50px;
    }
}

@media only screen and (max-width: 860px) {
    .nav-bar-container {
        height: 8vh;
    }

    .privacy-container>h3 {
        font-size: 16px;
    }

    .privacy-wrapper>p {
        font-size: 16px;
    }

    #tab-desk-privacy-txt-container {
        display: block;
    }

    .privacy-wrapper {
        width: fit-content;
        margin-left: auto;
        margin-right: auto;
    }

    /* Who we are start */


}

@media only screen and (min-width: 768px) {


    .whoweare-container>h2 {
        font-size: 28px;
    }
}



@media only screen and (min-width: 601px) and (max-width: 1024px) {

    /* portfolio start */
    #portfolio-introduction {
        margin-bottom: 3rem;
    }

    #portfolio-key-features {
        margin-top: 3rem;
    }

    #portfolio-container h1 {
        margin-bottom: 1.5rem;
    }

    .portfolio-card {
        width: 464px;
    }

    /* portfolio end */
    #service-main-container {
        margin-bottom: 2rem;
    }

    /* Landing Page Start */
    #tab-landing-img {
        display: block !important;
    }

    .main-text-wrapper {
        width: 100%;
    }

    #tab-desk-tagline-txt .tagline-txt {
        font-size: 45px;
        font-weight: bold;
        color: var(--taglineTextColor);
        font-family: 'Roboto Slab', sans-serif;
        text-align: center;
    }

    .main-container {
        margin-top: 0;
    }

    .mob-landing-static-txt {
        display: none;
    }

    #tab-desk-landing-static-txt {
        display: block;
        text-align: center;
        font-size: 20px;
        font-weight: var(--medium);
        font-family: 'Poppins', sans-serif;
        color: var(--landingStaticTextColor);

    }

    .headline-txt {
        font-size: 28px;
        font-weight: var(--medium);
        margin-bottom: 2rem;
    }

    .console-container {
        font-size: 35px;
        font-weight: var(--medium);
        text-align: center;
        margin-top: 1rem;
    }

    .btn-container {
        flex-direction: row;
        margin-left: auto;
        margin-right: auto;
        margin-top: 1.5rem;
    }

    .btn-container>input {
        margin-right: 3.1rem;
    }

    /* Landing page end */
    .careers-container {
        padding: 0 2rem;
    }

    .careers-msg p {
        width: auto;
    }

    .careers-msg {
        padding: 2rem;
        margin: 1rem auto;
    }

    #tab-project-svg {
        display: block;
    }

    .landing-services-container {
        margin-top: 3rem;
    }

    .process-container {
        margin-top: 2rem;
    }

    .tab-desk-body-txt {
        letter-spacing: 0;
    }

    #tab-desk-privacy-txt-container p {
        font-size: 14px;
        line-height: 25px;
    }

    /* clutch start */
    #tab-clutch {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: fit-content;
        margin-top: 2rem;
    }

    #tab-clutch>div {
        margin-left: 8rem;
    }

    #desk-clutch {
        display: none;

    }

    #client-txt {
        width: fit-content;
        margin: auto;
    }

    /* clutch end */
}

@media only screen and (max-width: 860px) {

    /* Landing Page start */
    .nav-bar>ul,
    .knock-container {
        display: none;
    }

    .main-container {
        display: block;
    }

    .main-text-wrapper {
        width: auto;
    }

    /* Services start */
    .services-main-container {
        padding-left: 1rem;
        padding-right: 1rem;
    }

    .services-container {
        width: 100%;
        column-gap: 2.5rem;
    }

    .tech-stack-container {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }


}

@media only screen and (min-width: 601px) and (max-width: 1024px) {
    .service-content-wrapper {
        align-items: end;
    }

    .service-content-container .img {
        width: 176px;
        height: 176px;
    }

    .service-content-container .img-container {
        display: flex;
    }

    #home-page {
        padding: 3rem 1rem 0 1rem;
    }

    #tab-desk-anim-tagline-txt-container h1 {
        text-align: center;
    }

