    }

    .tech-stack-wrapper>div {
        width: 100%;
        height: 100%;
        padding: 2rem;
    }

    /* our services */
    .services-icons-container p {
        font-size: 16px;
        letter-spacing: 1.12px;
        line-height: 20px;
    }

    .services-icons-container>div {
        min-height: 419px;
        max-width: 464px;
        justify-content: space-between;
    }

    .our-clients>div {
        max-width: 464px;
