    }

    /* our process */
    .process-container {
        margin-left: auto;
        margin-right: auto;
    }

    .our-process-mob-view {
        display: none;
    }

    .our-process-tab-view {
        display: block;
        width: fit-content;
        margin-left: auto;
        margin-right: auto;
    }

    .process-des-container {
        width: 100%;
    }

    .our-process-mob-view {
        display: none;
    }

    .our-process-tap-view {
        text-align: center;
    }
}

@media only screen and (min-width: 1025px) {
    .landing-page-container {
        height: calc(100vh - 68px);
    }

    .services-container {
        grid-template-columns: auto auto;
    }

    .services-icons-container {
        grid-template-columns: auto auto;
        grid-gap: 2rem 3rem;
    }

    .services-icons-container p {
        font-size: 16px;
        letter-spacing: 1.12px;
    }

    .our-clients p {
        font-size: 14px;
        letter-spacing: .98px;
        font-weight: 400;
    }

    .services-icons-container>div {
        width: 512px;
        height: 371px;
    }

    .our-clients {
        grid-template-columns: auto auto;
        grid-gap: 2rem 3rem;
    }

    .our-clients>div {
        width: 512px;
        height: 320px;
    }

    .services-icons-container .service-title {
        font-size: 18px;
    }

    .services-icons-container .bottom-txt {
        font-size: 16px;
    }

    #tab-desk-tagline-txt .tagline-txt {
        letter-spacing: 2px;
    }

    .main-text-container {
        margin-left: 80px;
        margin-top: 4.5rem;
    }

    #landing-desk-view {
        margin-top: 1.5rem;
    }

    #desk-project-svg {
        display: block;
    }

}