:root {
    --navBorderColor: #0000001e;
    --knockBgColor: #16d6ff;
    --textGreenColor: #00d28f;
    --taglineTextColor: #15CEFF;
    --taglineFullstopColor: #41D692;
    --landingStaticTextColor: #3E3E3E;
    --landingDynamicTextColor: #41D692;
    --headlineTextColor: #15CEFF;
    --bodyTextColor: #3E3E3E;
    --subheadlineTextColor: #15CEFF;
    --footerTextColor: #3E3E3E;
    --blue: #15CEFF;
    --footerBgColor: #f5f5f5;
    --BoxShadow: 0 0 6px #51515120;
    --regular: 400;
    --medium: 500;
    --semiBold: 600;
    --light: 300;
}

@media only screen and (max-width: 600px) {

    /* knock */
    #overlay {
        width: 100%;
        height: 100%;
        top: 0;
        padding: 0 1rem;
        border: none;
    }

    /* contact */
    #contact-form-subtitle {
        margin: 1rem 0 1.5rem 0;
    }

    #contact-msg-box-container {
        margin-bottom: 1.5rem;
    }

    #contact-container h2 {
        font-size: 14px;
        letter-spacing: 1.6px;
    }

    #contact-container .sm {
        font-size: 14px;
        letter-spacing: .56px;
    }

    #contact-container p {
        font: 400 12px 'Poppins', sans-serif;
        letter-spacing: .56px;
    }

    #contact-msg-box-container {
        flex-flow: column;
        row-gap: 1.5rem;
    }

    .contact-msg-box {
        width: 100%;
    }

    /* careers */
    .careers-container .careers-img {
        max-width: 360px;
        width: 100%;
        margin: auto;
    }

    /* Our trusted Allies */
    #our-trusted-allies {
        grid-template-columns: auto auto auto;
        padding: 1rem;
    }

    #our-trusted-allies img {
        width: 84px;
    }

    /* Our process Start */
    .body-txt-5 {
        display: block;
    }

    .tab-desk-body-txt-5 {
        display: none;
    }

    .our-process-mob-view {
        display: flex;
        overflow-y: scroll;
    }

    #tab-clutch {
        width: fit-content;
        margin: 2rem auto 5rem auto;
        display: none;

    }

    #tab-clutch>div {
        margin-left: 20%;

    }

    #client-txt {
        margin: 1.5rem;
    }

    .process-slider {
        scroll-behavior: smooth;
        /* transition: transform 2s; */
        /* -ms-overflow-style: none;  
     scrollbar-width: none; */
    }

    .process-dash-line {
        margin-top: auto;
        margin-bottom: auto;
    }

    /* Our process end */
    .main-container {
        margin: 0;
    }

    .landing-page-container {
        /* height: calc(100vh - 210px); */
    }

    #mob-tagline-txt .tagline-txt {
        color: var(--taglineTextColor);
        font-size: 24px;
        font-family: 'Roboto Slab', sans-serif;
        font-weight: bold;
        text-align: center;
        margin: 0;
        letter-spacing: 2.4px;
    }

    .txt-container {
        margin-top: 1rem;
    }

    .console-container {
        font-size: 25px;
    }

    #tab-desk-landing-static-txt {
        display: none;
    }

    .landing-services-container {
        /* margin-top: 0; */
    }

    #tab-desk-tagline-txt {
        display: none;
    }

    #mob-tagline-txt {
        display: block;
    }

    .mob-landing-static-txt {
        display: block;
    }

    .console-container {
        text-align: center;
        margin-top: 1rem;
    }

    .btn-container>a {
        margin: 0;
    }

    .headline-txt {
        font-size: 20px;
        margin-bottom: 1rem;
        letter-spacing: 1.6px;
    }

    .tab-desk-body-txt {
        display: none;
    }

    #mob-body-txt {
        display: block;
        font-size: 16px;
    }

    .process-container>.headline-txt {
        margin: 0 0 40px 0;
    }

    .sub-headline-txt {
        font-size: 16px;
    }

    .process-container {
        padding: 40px 0px;
    }

    .process-des-container {
        width: 100%;
        height: 12rem;
    }

    #animation {
        display: none;
    }

    /* Project details start */
    #project-count-container>div span:first-child {
        font-size: 40px;
    }

    #project-count-container>div span:last-child {
        font-size: 14px;
    }

    #project-details-container p {
        font-size: 14px;
    }

    #project-btn-container {
        flex-flow: column;
        gap: 1rem;
        align-items: center;
    }

    #pro-quoto-btn {
        padding: 11px 0px;
    }

    /*  portfolio start*/
    #portfolio-component-container .key-feature-bold {
        font-weight: 700;
    }

    #portfolio-component-container h1 {
        font-size: 20px;
        text-align: center;
    }

    #portfolio-introduction {
        margin-bottom: 1.5rem;
    }

    #portfolio-key-features {
        margin-top: 1.5rem;
    }

    #portfolio-main-container {
        flex-flow: column;
        justify-content: center;
        text-align: center;
    }

    #portfolio-main-container {
        row-gap: 1.5rem;
    }

    #offered-service-container {
        margin: auto;
    }

    #offered-service-container>div {
        justify-content: center;
    }

    #portfolio-main-container h1 {
        font-size: 20px;
        letter-spacing: 1.6px;
    }

    #portfolio-main-container h2 {
        font-size: 14px;
        letter-spacing: .56px;
    }

    #portfolio-main-container p {
        font-size: 12px;
        letter-spacing: .84px;
    }

    #portfolio-main-container h3 {
        font-size: 14px;
        font-weight: 600;
        letter-spacing: .93px;
        margin-bottom: 21px;
    }

    #portfolio-main-image {
        max-width: 359px;
        width: 100%;
    }

    .portfolio-card .name {
        font-size: 14px;
        font-weight: 600;
        letter-spacing: .56px;
    }

    .portfolio-card .type {
        font-size: 12px;
        letter-spacing: .82px;
    }

    .portfolio-card .description {
        font-size: 14px;
        font-weight: 500;
    }

    .portfolio-card .country {
        font-size: 12px;
        letter-spacing: .84px;
    }

    .portfolio-card-container {
        grid-template-columns: auto;
        margin-top: 1.5rem;
    }

    #portfolio-container h1 {
        font-size: 20px;
        margin-bottom: 1rem;
    }

    #portfolio-container p {
        font-size: 14px;
    }

    /* fortfolio end */
    /* Services container */
    #why-choose-us-container {
        box-shadow: none !important;
    }

    .service-content-container {
        box-shadow: none !important;
    }

    .service-content-wrapper {
        flex-flow: column;
        justify-content: center;
    }

    .service-content-container .img {
        width: 176px;
        height: 176px;
        margin-top: 1.5rem;
    }

    .service-content-container .sub-txt-2 {
        font-size: 12px;
    }

    .service-content-container h3 {
        font-size: 14px;
    }

    .service-content-container .sub-txt {
        font-size: 12px;
        margin-top: 1.5rem
    }

    .service-content-container .sub-txt {
        margin-bottom: 1.5rem;
    }

    #service-main-container {
        margin-bottom: 1.5rem;
    }

    #service-main-container img {
        width: 100%;
    }

    .services-icons-container {
        grid-template-columns: auto;
        row-gap: 1.5rem;
    }

    .our-clients {
        grid-template-columns: auto;
        row-gap: 1.5rem;
    }

    .services-icons-container>a {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 2rem;
    }

    .services-icons-container p {
        font-size: 12px;
        letter-spacing: 0.84px;
        line-height: 20px;
    }

    .services-icons-container .service-title,
    .bottom-txt {
        font-size: 14px;
    }

    #tab-desk-careers-msg-txt {
        display: none;
    }

    #mob-careers-msg-txt {
        display: block;
        text-align: center;
        font: 400 12px 'Poppins', sans-serif;
        letter-spacing: .84px;
        line-height: 18px;
    }

    .careers-container>h2 {
        text-align: center;
        font-size: 20px;
        margin-bottom: 1.5rem;
        letter-spacing: 1.6px;
    }

    .careers-container p {
        text-align: left;
        line-height: 20px;
        font-size: 12px;
        letter-spacing: 0.84px;
    }

    .careers-wrapper {
        flex-direction: column;
    }

    .careers-wrapper .txt-wrapper {
        margin-bottom: 1.5rem;
    }

    .careers-msg p {
        width: auto;
    }

    .careers-msg {
        padding: 2rem 2rem;
    }

    #careers-icon {
        margin-top: 1.5rem;
    }

    #mob-project-svg {
        display: block;
    }

    .whoweare-wrapper,
    .whatwedo-wrapper,
    .future-wrapper {
        display: block;
        width: 100%;
    }

    .who-we-are-img {
        width: 360px;
        max-width: 100%;
        margin: auto;
    }

    .who-we-are-map {
        width: 360px;
        max-width: 100%;
        margin: auto;
    }

    .whoweare-main .body-txt {
        text-align: left;
        font-family: var(--regular) 12px 'Poppins', sans-serif;
        line-height: 21px;
        letter-spacing: .84px;
    }

    .whoweare-container p {
        margin-top: 2rem;
    }
}


@media only screen and (max-width: 600px) {

    /* footer */
    .footer-item9 {
        display: none !important;
    }

    .footer-item8 {
        justify-content: center;
    }

    #submit-icon-container {
        justify-content: center;
        width: 299px;
        position: relative;
    }

    #email-submit-icon {
        right: 0px;
        width: 80px;
    }

    .services-main-container {
        margin-left: auto;
        margin-right: auto;
    }

    .services-container {
        display: block;
        width: fit-content;
    }

    #services-mob-txt {
        display: block;
    }

    #services-tab-desk-txt {
        display: none;
    }

    .services-main-container>p {
        font-size: 16px;
    }

    .service {
        width: 90%;
        height: 353px;
        padding: 1rem 1rem 1rem 1rem;
        margin: auto;
        margin-bottom: 2rem;
    }

    .service p {
        line-height: 25px;
    }

    .tech-stack-container {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }

    .tech-stack-wrapper {
        width: 76vw;
        grid-template-columns: 33vw 33vw;
        grid-template-rows: auto;
        grid-gap: 10vw;
        margin: auto;



    }

    .tech-stack-wrapper>div {
        width: 33vw;
        height: 33vw;
        padding: 2rem;

    }

    .tech-stack-container>div span {
        font-size: 10px;
    }

    .tech-stack-container p {
        font-size: 16px;
    }

    /* Contact start */
    .contact-form-main>h2 {
        font-size: 20px;
        margin-top: 0;
    }

    .contact-form>div>input,
    textarea {
        width: 90%;
        -webkit-appearance: none;
        box-shadow: inset 0px 0px 6px #00000020;

    }

    .contact-form-main p {
        font-size: 16px;
    }

    /* Privacy start */
    #mob-privacy-txt-container {
        display: block;
    }

    #mob-privacy-txt-container>p {
        font-size: 12px;
    }

    #tab-desk-privacy-txt-container {
        display: none;
    }
}